
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import {localapi, proapi, imgBaseUrl} from '../../config/env'
import {mobileCode, checkExsis, sendLogin, getcaptchas, accountLogin} from '../../service/getData'

export default {
components: {
Group,
XInput,
XButton,
Blur,
Divider,
Flexbox,
FlexboxItem
},
data () {
return {
title: '',
loginSrc:require('../../assets/logo.png'),
loginWay: false, //
showPassword: false, // 
phoneNumber: null, //
mobileCode: null, //
validate_token: null, //
computedTime: 0, //
userInfo: null, //
userAccount: null, //
passWord: null, //
captchaCodeImg: null, //
codeNumber: null, //
showAlert: false, //
alertText: null //
}
},
created: function() {

},
methods: {
showLoading () {
this.$vux.loading.show({
text: 'Loading'
})
var url = 'http://celelink.com:8088/cum/backend/api/address/getUserAddress'
this.$ajax({
type:'post',
url:url,
'userName':'xuwen',
'password':'123'
})
.then(function (data) {
console.log(data.request)
})
setTimeout(() => {
this.$vux.loading.hide()
}, 20000)
}
},
mounted () {
let w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
let h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
this.$refs.nana.style.height = h +'px';
this.$refs.nana.style.width = w +'px';
}
}



